import React from "react";
import { useTheme } from "@mui/material";
import Typography from "@mui/material/Typography";

export enum TYPOGRAPHY_TITLE_KIND {
  Primary = "Primary",
  Secondary = "Secondary"
}

const TypographyTitle = ({
                           kind = TYPOGRAPHY_TITLE_KIND.Primary,
                           children,
                           sx = {},
                           // Any additional props will be passed to the
                           // underlying material-ui component.
                           ...rest
                         }) => {
  const { typography, palette } = useTheme();

  return (
    // @ts-ignore
    <Typography
      component="div"
      sx={{
        ...(kind === TYPOGRAPHY_TITLE_KIND.Primary ? {
          lineHeight: "48px",
          fontSize: 39,
          fontWeight: 700,
          ['@media(max-width: 780px)'] : {
            fontSize: 24,
          }
        } : {
          fontSize: 30,
          lineHeight: "18px",
          fontWeight: 600,
          ['@media(max-width: 780px)'] : {
            fontSize: 20
          }
        }),
        letterSpacing: "1.2px",
        fontFamily: typography.fontFamily,
        color: palette.common.white,
        ...sx
      }}
      variant={kind === TYPOGRAPHY_TITLE_KIND.Primary ? "h1" : "h2"}
      {...rest}
    >
      {children}
    </Typography>
  );
};

export default TypographyTitle;
