import React from "react";
import { useTheme } from "@mui/material";
import Typography from "@mui/material/Typography";
import { OverridableComponent } from "@mui/material/OverridableComponent";

export enum TYPOGRAPHY_BODY_SIZE {
  Small = "Small",
  Regular = "Regular",
  Large = "Large"
}

// TODO: Update these values with actual values from Figma
const typographyBodyHashMap = new Map([
  [
    TYPOGRAPHY_BODY_SIZE.Small,
    { lineHeight: 26, fontSize: 16 }
  ],
  [
    TYPOGRAPHY_BODY_SIZE.Regular,
    { lineHeight: 30, fontSize: 21 }
  ],
  [
    TYPOGRAPHY_BODY_SIZE.Large,
    { lineHeight: 32, fontSize: 24 }
  ]
]);

const TypographyBody = ({
                          size = TYPOGRAPHY_BODY_SIZE.Regular,
                          children,
                          component = "div",
                          sx = {},
                          // Any additional props will be passed to the
                          // underlying material-ui component.
                          ...rest
                        }) => {
  const { typography, palette } = useTheme();

  return (
    <Typography
      component={component as unknown as OverridableComponent<any>}
      sx={{
        fontSize: `${typographyBodyHashMap.get(size)!.fontSize}px`,
        color: palette.common.white,
        fontFamily: typography.fontFamily,
        lineHeight: `${typographyBodyHashMap.get(size)!.lineHeight}px`,
        whiteSpace: "pre",
        ...sx
      }}
      {...rest}
    >
      {children}
    </Typography>
  );
};

export default TypographyBody;
