import React from "react";
import { useTheme } from "@mui/material";
import Typography from "@mui/material/Typography";

export enum TYPOGRAPHY_SUBTITLE_KIND {
  Uppercase = "Uppercase",
  Titlecase = "Titlecase"
}

const TypographySubtitle = ({
                              kind = TYPOGRAPHY_SUBTITLE_KIND.Uppercase,
                              sx = {},
                              children,
                              // Any additional props will be passed to the
                              // underlying material-ui component.
                              ...rest
                            }) => {
  const { typography, palette } = useTheme();

  return (
    // @ts-ignore
    <Typography
      component="div"
      sx={{
        ...sx,
        fontFamily: typography.fontFamily,
        textTransform: kind === TYPOGRAPHY_SUBTITLE_KIND.Uppercase
          ? "uppercase"
          : "capitalize",
        opacity: 0.5,
        fontSize: 14,
        letterSpacing: "1.2px",
        lineHeight: "18px",
        color: palette.common.white
      }}
      variant="h3"
      {...rest}
    >
      {children}
    </Typography>
  );
};

export default TypographySubtitle;
