import { WasmAPI } from "@terra-money/terra.js";
import { LCDClient } from "@terra-money/terra.js";

// CW-721 contract debugging helpers
export const logAllTokens = async (
  network: any,
  nftContractAddress: string
) => {
  console.log("netowrk is ", network);
  const all_tokens = await getApi(network).contractQuery(nftContractAddress, {
    all_tokens: { limit: 100 },
  });
  console.log("nft.all_tokens (limit: 100): ", all_tokens);
};

export const logContractInfo = async (
  network: any,
  nftContractAddress: string
) => {
  const contract_info = await getApi(network).contractQuery(
    nftContractAddress,
    {
      contract_info: {},
    }
  );
  console.log("nft.contract_info: ", contract_info);
};

const getApi = (network: any): any => {
  const lcd_client = new LCDClient({
    URL: network?.lcd || "",
    chainID: network?.chainID || "",
  });
  const api = new WasmAPI(lcd_client.apiRequester);
  return api;
};
