import { useCallback, useState } from "react";
import { logAllTokens, logContractInfo } from "../../utils/contract_utils";
import { MsgExecuteContract } from "@terra-money/terra.js";
import {
  CreateTxFailed,
  Timeout,
  TxFailed,
  TxResult,
  TxUnspecifiedError,
  useConnectedWallet,
  UserDenied
} from "@terra-money/wallet-provider";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Link from "@mui/material/Link";
import CardActions from "@mui/material/CardActions";
import { DialogTitle } from "@mui/material";
import Box from "@mui/material/Box";
import { Img } from "react-image";
import { BOMBAY_CONFIG } from "../../env";
import TypographyTitle, { TYPOGRAPHY_TITLE_KIND } from "../pixyComponents/typography/TypographyTitle";
import { truncate } from "../../utils/truncate";
import { PixyButton, PixyModal } from "../pixyComponents";
import TypographySubtitle, { TYPOGRAPHY_SUBTITLE_KIND } from "../pixyComponents/typography/TypographySubtitle";

export interface BuyNFTModalProps {
  // children: ReactNode;
  title?: string;
  ustPrice: number;
  imageUrl: string;
  modalOpen: boolean;
  setModalOpen: (modalOpen: boolean) => void;
  // description: string;
}

export const BuyNFTModal = ({
                              title,
                              ustPrice,
                              // description,
                              imageUrl,
                              modalOpen,
                              setModalOpen
                            }: BuyNFTModalProps) => {
  const [txResult, setTxResult] = useState<TxResult | null>(null);
  const [txError, setTxError] = useState<string | null>(null);
  const connectedWallet = useConnectedWallet();
  const nftContractAddress = BOMBAY_CONFIG.ADDRESSES.nft;

  if (connectedWallet) {
    // DEBUGGING
    (async () => {
      await logAllTokens(connectedWallet?.network, nftContractAddress);
      await logContractInfo(connectedWallet?.network, nftContractAddress);
    })();
  }

  const handleCancel = () => {
    setModalOpen(false);
  };

  // Tutorial:
  // https://codesandbox.io/s/github/terra-money/wallet-provider/tree/main/templates/create-react-app?file=/src/components/TxSample.tsx:2074-2082
  const handleBuy = useCallback(() => {
    if (!connectedWallet) {
      return;
    }

    if (connectedWallet.network.chainID.startsWith("columbus")) {
      alert(`We're not ready for production yet! Switch to Bombay/testnet.`);
      return;
    }

    setTxResult(null);
    var transferNFTTxnMsg = new MsgExecuteContract(
      connectedWallet.terraAddress,
      nftContractAddress,
      {
        mint: {}
      },
      {}
    );

    connectedWallet
      .post({
        msgs: [transferNFTTxnMsg]
      })
      .then((nextTxResult: TxResult) => {
        console.log("Executed mint: ", nextTxResult);
        setTxResult(nextTxResult);
      })
      .catch((error: unknown) => {
        console.log("Caught error: ", error, typeof error);
        if (error instanceof UserDenied) {
          setTxError("You declined the transaction.");
        } else if (error instanceof CreateTxFailed) {
          setTxError("Failed to pledge: " + error.message);
        } else if (error instanceof TxFailed) {
          setTxError("The transaction failed: " + error.message);
        } else if (error instanceof Timeout) {
          setTxError(
            "The transaction timed out. Please try again. " + error.message
          );
        } else if (error instanceof TxUnspecifiedError) {
          setTxError("Something went wrong: " + error.message);
        } else {
          setTxError(
            "Unknown Error: " +
            (error instanceof Error ? error.message : String(error))
          );
        }
      });
  }, [connectedWallet]);

  return (
    <>
      <PixyModal
        open={modalOpen}
        onClose={() => setModalOpen(false)}
      >
        <DialogTitle>You are purchasing</DialogTitle>
        <Img width={368} src={imageUrl} />
        <Box className="flex flex-col items-center" sx={{ mt: 4, mb: 1 }}>
          <TypographySubtitle
            sx={{ my: 2, fontSize: "24px" }}
            kind={TYPOGRAPHY_SUBTITLE_KIND.Uppercase}
          >
            Title
          </TypographySubtitle>
          <TypographyTitle
            kind={TYPOGRAPHY_TITLE_KIND.Secondary}
            sx={{ margin: "8px 0 40px 0" }}
          >
            #{title!.toUpperCase()}
          </TypographyTitle>
        </Box>
        <Box className="flex flex-col items-center" sx={{ my: 1 }}>
          <TypographySubtitle
            sx={{ my: 2, fontSize: "24px" }}
            kind={TYPOGRAPHY_SUBTITLE_KIND.Uppercase}
          >
            Price
          </TypographySubtitle>
          <TypographyTitle
            kind={TYPOGRAPHY_TITLE_KIND.Secondary}
            sx={{ margin: "8px 0 40px 0" }}
          >
            {ustPrice.toLocaleString()} UST
          </TypographyTitle>
        </Box>
        <Box className="w-full" sx={{ maxWidth: 500, px: 4 }}>
          <PixyButton
            disabled={!connectedWallet?.availablePost}
            onClick={handleBuy}
            fullWidth
          >
            Buy!
          </PixyButton>
        </Box>
        <Box className="w-full" sx={{ maxWidth: 500, mt: 2, px: 4 }}>
          <PixyButton
            onClick={handleCancel}
            disabled={!connectedWallet?.availablePost}
            fullWidth
            kind="secondary"
          >
            Cancel
          </PixyButton>
        </Box>
        <CardActions>
          {txResult && (
            <Typography sx={{ fontSize: 16 }} color="primary.light">
              Success!
              <Link
                href={`https://finder.terra.money/${connectedWallet?.network.chainID}/tx/${txResult.result.txhash}`}
                color="secondary.dark"
                underline="none"
                target="_blank"
                rel="noreferrer"
                sx={{ mr: 10, fontWeight: "bold" }}
              >
                {" " + truncate(txResult.result.txhash || "", 12) + " ðŸ”—"}
              </Link>
            </Typography>
          )}
          {txError && (
            <Typography sx={{ fontSize: 16 }} color="error.dark">
              {txError}
            </Typography>
          )}
          {(txResult || txError) && (
            <>
              <Button
                color="primary"
                onClick={() => {
                  setTxResult(null);
                  setTxError(null);
                  return null;
                }}
              >
                Clear
              </Button>
            </>
          )}
          {!connectedWallet && <p>Wallet not connected!</p>}
          {connectedWallet && !connectedWallet.availablePost && (
            <p>Can not post Tx</p>
          )}
        </CardActions>
      </PixyModal>
    </>
  );
};
