import { ReactNode, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import Box from "@mui/material/Box";
import { PixyButton, PixyNFTCard } from "../components/pixyComponents";
import { BuyNFTModal } from "../components/BuyNFTModal/BuyNFTModal";
import { MockNFTDatastore } from "../MockNFTDatastore";
import TypographyTitle, { TYPOGRAPHY_TITLE_KIND } from "../components/pixyComponents/typography/TypographyTitle";
import TypographySubtitle, { TYPOGRAPHY_SUBTITLE_KIND } from "../components/pixyComponents/typography/TypographySubtitle";
import TypographyBody
  from "../components/pixyComponents/typography/TypographyBody";

export interface CampaignNFTProps {
  children: ReactNode;
}

type CampaignNFTParams = {
  campaignSlug: string;
  nftSlug: string;
};

export const CampaignNFT = () => {
  const [modalOpen, setModalOpen] = useState(false);

  const { campaignSlug, nftSlug } = useParams<CampaignNFTParams>();
  const nft = MockNFTDatastore[nftSlug];
  const imageUrl = nft ? nft.image : "";
  const title = nft ? nft.title : "UNTITLED";
  const ustPrice = nft ? nft.ustPrice : "9999999999";
  const isVariablePrice = nft ? nft.isVariablePrice : false;
  const shortDescription = nft ? nft.shortDescription : "";
  const description = nft ? nft.description : "";
  const history = useHistory();

  return (
    <>
      <Box
        component="section"
        sx={{
          display: "flex",
          margin: "100px auto 32px auto",
          width: 1322
        }}
      >
        <Box>
          <PixyNFTCard
            campaignSlug={campaignSlug}
            nftSlug={nftSlug}
            key={imageUrl}
            image={imageUrl}
            title={title}
            ustPrice={ustPrice}
            isVariablePrice={isVariablePrice}
            shortDescription={shortDescription}
          />
        </Box>
        <Box
          className="flex flex-col"
          sx={{
            px: 12,
            width: 620
          }}
        >
          <Box>
            <TypographySubtitle kind={TYPOGRAPHY_SUBTITLE_KIND.Uppercase}>
              Danku Zone
            </TypographySubtitle>
          </Box>
          <Box>
            <TypographyTitle
              kind={TYPOGRAPHY_TITLE_KIND.Primary}
              sx={{ margin: "8px 0 40px 0" }}
            >
              {title.toUpperCase()} NFT
            </TypographyTitle>
          </Box>
          <Box>
            <TypographyBody sx={{ opacity: 0.8 }}>{description}</TypographyBody>
          </Box>
          <Box sx={{ mt: 4 }}>
            <BuyNFTModal
              modalOpen={modalOpen}
              setModalOpen={setModalOpen}
              title={title}
              ustPrice={ustPrice}
              imageUrl={imageUrl}
            />
            <PixyButton
              upperCase={false}
              onClick={() => setModalOpen(true)}
            >
              Buy this NFT
            </PixyButton>
            <PixyButton
              onClick={() => history.push("/how-it-works")}
              sx={{ ml: 2 }}
              upperCase={false}
              variant="outlined"
            >
              How It Works
            </PixyButton>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default CampaignNFT;
